.four-news {
   display: flex;
   flex-direction: column;
   background-color: #fff;
   padding: 0 1rem;

   @media screen and (min-width: 980px) {
      display: none;
   }

   .four-news__container {
      display: flex;
      padding: 5px 0;
      border-bottom: 2px solid #eaeeef;

      @media screen and (min-width: 500px) {
         align-items: center;
      }

      .four-news__img {
         width: 12rem;
         height: 8rem;
      }

      .four-news__h3 {
         width: 207px;
         height: 80px;
         margin-left: 8px;
         font-size: 1.3rem;
         line-height: 1.7rem;

         @media screen and (min-width: 500px) {
            font-size: 1.6rem;
         }

         @media screen and (min-width: 675px) {
            font-size: 1.8rem;
         }

         span {
            text-transform: uppercase;
            color: #eb1701;
         }
      }
   }
}
